import React from 'react';
import { withRouter } from 'react-router-dom';

// antd
import { Table, Icon } from 'antd';

// styled-components
import {
  GridTableLayout,
  GridPosition,
  TableBg,
} from './style';

// css
import './style.css';

// immutable
import Immutable from 'immutable';
import isEmpty from 'lodash/isEmpty';

@withRouter
class GridTable extends React.Component {
  state = {
    rowId: '', // id
  };

  render() {
    const groupColumns = [
      {
        title: '',
        dataIndex: 'icon',
        key: 'icon',
        render: () => <Icon type='hdd' />
      },
      {
        title: '',
        dataIndex: 'domain',
        key: 'domain'
      },
      {
        title: '',
        dataIndex: 'state',
        key: 'state'
      },
      {
        title: '',
        dataIndex: 'dtype',
        key: 'dtype',
        render: (text, record) => record.recvServicePort ? <span></span> : <span>S4000</span>
      },
      {
        title: '',
        dataIndex: 'port',
        key: 'port',
      }
    ];
    return (
      <GridTableLayout>
        <GridPosition>
          <TableBg>
            <Table
              id='deviceTable'
              dataSource={this.props.unitList}
              columns={groupColumns}
              pagination={false}
              rowKey={record => record.id}
              rowClassName={this.setTableRowClassName}
              onRow={this.onClickTableRow}
            />
          </TableBg>
        </GridPosition>
      </GridTableLayout>
    )
  }

  // 
  setTableRowClassName = (record) => {
    return record.id === this.state.rowId ? 'clickRowStyle' : '';
  };

  // 
  onClickTableRow = (record) => {
    return {
      onClick: (event) => {
        this.setState({ rowId: record.id }); // css
        this.props.onClick(record);
      }
    }
  };

  // , table
  loadDefaultTableItem = (unitList) => {
    const firstItem = unitList[0];
    this.setState({
      rowId: firstItem.id,
    }, () => {
      // this.onClickTableRow(firstItem); // 
      this.setTableRowClassName(firstItem); // css
      this.props.onClick(firstItem); // 
    });
  };

  // , table
  componentDidMount() {
    const unitList = this.props.unitList;
    if (unitList.length !== 0) {
      this.loadDefaultTableItem(unitList);
    }
  }

  shouldComponentUpdate(nextProps, nextState, nextContext) {
    const currentList = Immutable.fromJS(this.props.unitList);
    const nextList = Immutable.fromJS(nextProps.unitList);
    if (Immutable.is(currentList, nextList)) { // componentDidUpdate
      return false;
    }
    return true;
  }

  // , rowIdunitList
  // : , rowId
  // : unitList
  componentDidUpdate(prevProps, prevState, snapshot) {
    const unitList = this.props.unitList;
    if (unitList.length !== 0) {
      const rowId = this.state.rowId;
      if (isEmpty(rowId)) {
        // : unitList
        this.loadDefaultTableItem(unitList);
      } else {
        // : rowId
        const index = unitList.findIndex((item) => item.id === rowId);
        if (index > -1) {
          // 
          const item = unitList[index];
          // rowId
          this.props.onClick(item);
        }
        else {
          // 
        }
      }
    }
  }

  componentWillUnmount() {
  }
}

export default GridTable;