import { fromJS } from 'immutable';
import { types as authTypes } from './auth.redux';

const defaultState = fromJS({
  requestQuantity: 0, // API
  error: null, // 
  redirectTo: '', // 
  isFetching: false, // (wowzatabinfo.js, wstream.redux)
  isGroupInfoFetching: false, // (gridcard.js, fetchGroupInfo)
});

// action types
export const types = {
  START_REQUEST: 'APP/START_REQUEST',
  FINISH_REQUEST: 'APP/FINISH_REQUEST',
  SET_ERROR: 'APP/SET_ERROR',
  REMOVE_ERROR: 'APP/REMOVE_ERROR',
  START_WOWZA_FETCH: 'APP/START_WOWZA_FETCH',
  FINISH_WOWZA_FETCH: 'APP/FINISH_WOWZA_FETCH'
};

// action creators
export const actions = {
  startRequest: () => ({
    type: types.START_REQUEST
  }),
  finishRequest: () => ({
    type: types.FINISH_REQUEST
  }),
  setError: (error) => ({
    type: types.SET_ERROR,
    error
  }),
  removeError: () => ({
    type: types.REMOVE_ERROR
  }),
  startFetching: () => ({
    type: types.START_WOWZA_FETCH
  }),
  finishFetching: () => ({
    type: types.FINISH_WOWZA_FETCH
  }),
  startGroupInfoFetching: () => ({
    type: types.START_GROUP_INFO_FETCH
  }),
  finishGroupInfoFetching: () => ({
    type: types.FINISH_GROUP_INFO_FETCH
  }),
};

// reducers
export default (state = defaultState, action) => {
  switch (action.type) {
    case types.START_REQUEST:
      return state.merge({ requestQuantity: state.get('requestQuantity') + 1 });
    case types.FINISH_REQUEST:
      return state.merge({ requestQuantity: state.get('requestQuantity') - 1 });
    case types.SET_ERROR:
      return state.merge({ error: action.error });
    case types.REMOVE_ERROR:
      return state.merge({ error: null });
    case authTypes.LOGIN:
      return state.merge({ redirectTo: '/dashboard' });
    case authTypes.LOGOUT:
    case authTypes.MODIFY_PASSWORD:
      return state.merge({ redirectTo: '' });
    case types.START_WOWZA_FETCH:
      return state.merge({ isFetching: true }); // 
    case types.FINISH_WOWZA_FETCH:
      return state.merge({ isFetching: false });
    case types.START_GROUP_INFO_FETCH:
      return state.merge({ isGroupInfoFetching: true }); // 
    case types.FINISH_GROUP_INFO_FETCH:
      return state.merge({ isGroupInfoFetching: false });
    default:
      return state;
  }
};

